
import {defineComponent} from 'vue';
import Text from '@ohrm/oxd/core/components/Text/Text.vue';

export default defineComponent({
  name: 'oxd-branding',

  props: {
    url: {
      type: String,
      default: '/',
    },
    imageSrc: {
      type: String,
      default: '',
    },
    toggle: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    'oxd-text': Text,
  },

  computed: {
    classes(): object {
      return {
        toggled: this.toggle,
      };
    },
  },
});
